// main.sass
@import "reset";

@import url('https://fonts.googleapis.com/css?family=Roboto|Pacifico');

// @VARS
$bodyfont: 'Roboto', sans-serif;
$headingfont: 'Pacifico', cursive;

$lightblue: #00ACA5;
$darkblue: #282E5E;
$orange: #E35014;

// @GLOBAL
html,
body {
	font-size: 10px;
	color: $darkblue;
	font-family: $bodyfont; }

body {
	line-height: 1.2;
	background-color: #FDC743;
 }	//background: linear-gradient(#8A4E72, #ED6E44, #FABA5C, #FFFFFF)

main {
	margin-bottom: 100px; }

.flow {
	max-width: 840px;
	box-sizing: border-box;
	margin: 0 auto;
	padding: 0 20px; }

h1, h2, h3, h4, h5, h6 {
	font-family: $headingfont;
	margin: 40px 0 10px;
	span {
		display: block; } }

h1 {
	text-align: center;
	color: white;
	font-size: 6em;
	span {
		color: $darkblue;
		text-transform: uppercase;
		img {
			width: 100%;
			height: auto; } } }

h2 {
	font-size: 3em;
	color: white;
	a {
		color: white;
		border-bottom: 1px dotted white; } }

h3 {
	font-size: 2em; }

p {
	font-size: 1.2em;
	margin: 20px 0; }
p:first-of-type {
	margin-top: 40px; }
p:last-of-type {
	margin-bottom: 40px; }
p.instructions {
	margin: 10px 0 20px; }

label {
	font-size: 1.2em; }

li {
	font-size: 1.4em; }

.owl {
	width: 80px;
	height: auto; }

img.leaves {
	position: fixed;
	top: 0;
	z-index: -200;
	width: auto;
	height: 80vh; }
img#leavesLeft {
	left: 0; }
img#leavesRight {
	right: 0;
	transform: scaleX(-1) {
  filter: FlipH; } }
@media (min-width: 800px) {
	img.leaves {
		height: 120vh; } }

header {
	.banner {
		display: grid;
		grid-template: auto / 160px auto;
		margin-bottom: 40px;
		#logo {
			grid-column: 1 / 2;
			justify-self: flex-start;
			width: 160px;
			height: 160px; }
		.social {
			grid-colum: 2 / 3;
			justify-self: flex-end;
			ul {
				display: flex; }
			li {
				flex: 0 1 auto;
				margin: 20px 10px; }
			img {
				height: 30px;
				width: auto; } } }

	section#uptoyou {
		p {
			color: white; } } }

@import "matchgame";
@import "multichoice";
@import "wordpuzzle";

#game1 {
	h2 {
		span {
			color: $lightblue; } } }

#game3 {
	h2 {
    color: $lightblue; } }

#contest {
	padding-bottom: 60px;
	h2 {
		span {
			color: $lightblue; } } }

#contestForm {
	border: 40px solid;
	border-image-source: url("../img/smoke.svg");
	border-image-slice: 100 fill;
	border-image-width: 6em;
	border-image-outset: 4em 4em 10em;
	margin: 80px 0;
	fieldset {
		margin: 20px 0; }
	#submit {
		margin: 0;
		text-align: center; }
	#consent {
		display: flex;
		flex-flow: row nowrap;
		width: 80%;
		margin: 20px auto;
		input {
			margin-right: 20px; }
		label {
			display: inline-block; }
		input,
		label {
			flex: 1 1 auto; }
		input > label {
			display: none; } }
	textarea {
		max-width: 100%;
		width: 100%;
		min-height: 200px;
		padding: 20px;
		resize: vertical;
		box-sizing: border-box; }
	input,
	textarea {
		background-color: transparent;
		border-radius: 5px;
		border: 1px solid $darkblue; }
	input {
		padding: 3px 20px; }
	input[type="submit"] {
		color: white;
		font-family: $headingfont;
		font-size: 1.4em;
		background-color: $lightblue;
		border-radius: 10px;
		cursor: pointer;
		border: none; }
	#contactInfo {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
		input {
			flex: 1 0 calc(100% / 3 - 62px);
			margin: 10px; } } }

#incoweb {
	.flow {
		cursor: pointer; } }

#incoweb,
#topito {
	img {
		width: 100%;
		height: auto; } }
#competence,
#mission,
#whocanfile,
#DDD {
	ul {
		list-style: disc inside;
		padding-left: 1.2em;
		margin: 40px 0;
		li {
			font-size: 1.2em;
			margin-bottom: 1.2em; } } }

#extra .flow {
	display: grid;
	grid-template: auto auto / auto;
	row-gap: 20px; }
#downloads,
#radio {
	h2 {
		margin-bottom: 40px;
		img {
			vertical-align: middle;
			height: 80px;
			width: auto;
			margin-right: 10px; } } }

#downloads {
	grid-row: 1 / 2;
	ul {
		padding-left: 1.4em; }
	li {
		margin-bottom: 1.4em;
		&:before {
			content: '';
			display: inline-block;
			height: 1.4em;
			width: 1.4em;
			background-image: url('../img/download.svg');
			background-size: contain;
			background-repeat: no-repeat;
			margin-right: 0.7em; }
		a {
			color: $darkblue;
			font-size: 1.4em; } } }

#radio {
	grid-row: 2 / 3; }

@media (min-width: 800px) {
	#extra .flow {
		grid-template: auto / 50% 50%;
		column-gap: 20px; }
	#downloads {
		grid-row: 1 / 2;
		grid-column: 1 / 2; }
	#radio {
		grid-row: 1 / 2;
		grid-column: 2 / 3; } }

hr {
	margin: 40px auto;
	&.sep {
		max-width: 800px;
		border-top: 20px solid;
		border-image-source: url("../img/separator.svg");
		border-image-slice: 20 0;
		border-image-width: 10em;
		border-image-outset: 2em; }
	&.sepalt {
		max-width: 250px;
		border-top: 40px solid;
		border-image-source: url("../img/separator-alt.svg");
		border-image-slice: 40 0;
		border-image-width: 10em;
		border-image-outset: 2em; } }

@media (min-width: 800px) {
	hr {
		&.sep {
			border-top: 40px solid;
			border-image-slice: 40 0; } } }

footer {
	padding: 40px 0;
	background-color: #E35014;
	border-top: 1px solid;
	border-image: url("../img/footer.png") 100% 0 100% / auto / 78px stretch;
	.grid {
		display: grid;
		grid-template: auto auto / auto;
		grid-row-gap: 20px;
		img#signature {
			grid-row: 1 / 2;
			width: 302px;
			height: auto; }
		.links {
			grid-row: 2 / 3;
			ul.social {
				display: flex;
				li {
					flex: 0 1 auto;
					margin-right: 20px;
					img {
						height: 30px;
						width: auto; } } }
			ul#links {
				display: flex;
				li {
					flex: 0 1 auto;
					padding-right: 10px;
					margin-right: 10px;
					border-right: 1px solid white;
					color: white;
					a {
						color: white; } }
				li:last-of-type {
					border-color: transparent; } } } }
	@media (min-width: 800px) {
		.grid {
			grid-template: auto / 302px auto;
			grid-column-gap: 80px;
			img#signature {
				grid-column: 1 / 2;
				grid-row: 1 / 2; }
			.links {
				grid-column: 2 / 3;
				grid-row: 1 / 2; } } } }
